nav {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    top: 0px;
    height: 80px;
    border-style: solid;
    border-width: 0;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-line-pack: center;
        align-content: center;
    padding: 0;
    padding-bottom: 0px;
    border-bottom: 1px solid #42282F;
    color: #D6655A;
}

nav h1 {
    display: inline-block;
    font-size: 50px;
}

nav a {
    color: inherit;
    text-decoration: none;
    font-size: 30px;
}

nav a:hover, nav a:focus {
    color: #FFB2A7;
}


