footer {
    bottom: 0;
    min-height: 100px;
    left: 0;
    position: absolute;
    width: 100%;
    background-color: #42282F;
    color: #D6CCAD;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 20px 0 30px 0;
}

footer a {
  text-decoration: none;
  color: inherit;
  font-size: inherit;
  margin-right: 5px;
}

footer a:hover, footer a:focus {
  color: #FFB2A7;
}


